html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Monstserrat", sans-serif;
  font-size: 1.6rem;
}
body.fijo {
  overflow: hidden;
}
body.fix-scroll-nav {
  padding-top: 6rem;
}

p {
  margin: 0;
  color: #000000;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  width: 100%;
  height: auto;
  display: block;
}

h1, h2, h3, h4 {
  margin: 0 0 5rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

.navegacion-principal a {
  color: #ffffff;
  font-size: 2.4rem;
}
.navegacion-principal a:hover {
  color: #FEF46D;
}

.header {
  background-color: #4CB8B3;
  text-align: center;
  transition-property: background-color;
  transition-duration: 0.3s;
}
@media (min-width: 768px) {
  .header.fijo {
    position: fixed;
    top: 0;
    width: 100%;
    box-shadow: 0 2px 9px #000000;
    background-color: rgba(76, 184, 179, 0.95);
  }
  .header.fijo:hover {
    background-color: #4CB8B3;
  }
}
.header h1 {
  color: #ffffff;
  font-size: 3.4rem;
}

.contenido-header {
  padding: 1rem;
}
@media (min-width: 768px) {
  .contenido-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
@media (min-width: 768px) {
  .contenido-header h1 {
    margin: 0;
  }
}

.navegacion-principal a {
  display: block;
  margin-bottom: 1rem;
}
@media (min-width: 768px) {
  .navegacion-principal a {
    margin: 0;
  }
}
@media (min-width: 768px) {
  .navegacion-principal {
    display: flex;
    gap: 2rem;
  }
}

.video {
  position: relative;
  max-height: 60rem;
  overflow: hidden;
}
.video .overlay {
  position: absolute;
  /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#f203c1+0,7305f0+100&0.3+0,0.5+100 */
  background: linear-gradient(350deg, rgba(242, 3, 193, 0.3) 0%, rgba(115, 5, 240, 0.7) 100%);
  /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  background: -moz-linear-gradient(350deg, rgba(242, 3, 193, 0.3) 0%, rgba(115, 5, 240, 0.7) 100%);
  /* FF3.6-15 */
  background: -webkit-linear-gradient(350deg, rgba(242, 3, 193, 0.3) 0%, rgba(115, 5, 240, 0.7) 100%);
  /* Chrome10-25,Safari5.1-6 */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr="#4df203c1", endColorstr="#807305f0",GradientType=1 );
  /* IE6-9 fallback on horizontal gradient */
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
}
.video video {
  width: 100%;
  min-width: 60rem;
  display: block;
}

.contenido-video {
  z-index: 1;
}
.contenido-video h2 {
  color: #ffffff;
  margin: 0;
  font-size: 4rem;
}
@media (min-width: 768px) {
  .contenido-video h2 {
    font-size: 6rem;
  }
}
.contenido-video p {
  background-color: #F53756;
  display: inline-block;
  padding: 1rem;
  border-radius: 0.3rem;
  color: #ffffff;
  font-size: 2rem;
}

.sobre-festival {
  margin: 5rem auto;
}
@media (min-width: 768px) {
  .sobre-festival {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}

.contenido-festival {
  margin-top: 3rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 3rem;
}
@media (min-width: 768px) {
  .contenido-festival {
    margin: 0;
  }
}
.contenido-festival h2 {
  margin: 0;
  text-align: center;
}
.contenido-festival .fecha {
  color: #F53756;
  font-weight: 700;
}

.lineup {
  background-color: #F53756;
  padding: 5rem 0;
}
.lineup h3, .lineup p {
  color: #ffffff;
  text-align: center;
}
.lineup .dia {
  text-transform: uppercase;
  margin: 3rem 0;
}

@media (min-width: 768px) {
  .contenedor-escenarios {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}

.escenario {
  padding-top: 3rem;
  margin-bottom: 3rem;
}
.escenario:last-of-type {
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .escenario {
    margin-bottom: 0;
  }
}
.escenario .nombre-escenario {
  color: #000000;
}
.escenario .nombre-escenario:after {
  content: "";
  display: block;
  width: 20rem;
  height: 10rem;
  margin: 3rem auto 0 auto;
  background-size: 15rem;
  background-position: center center;
  background-repeat: no-repeat;
}

.rock .nombre-escenario:after {
  background-image: url(../img/icono_rock.png);
}

.edm .nombre-escenario:after {
  background-image: url(../img/icono_edm.png);
}

.calendario {
  list-style: none;
  margin: 0;
  padding: 0;
}
.calendario li {
  padding: 2rem;
  font-weight: 700;
}
.calendario li span {
  font-weight: 400;
}

.bg-amarillo {
  background-color: #FEF46D;
}
.bg-amarillo li:nth-child(odd) {
  background-color: #fdee21;
}

.bg-verde {
  background-color: #4CB8B3;
}
.bg-verde li:nth-child(odd) {
  background-color: #3b9691;
}

.contenedor-galeria {
  margin: 5rem auto;
}
.contenedor-galeria h3 {
  text-align: center;
}

.galeria-imagenes {
  list-style: none;
  padding: 0;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
}
@media (min-width: 480px) {
  .galeria-imagenes {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 768px) {
  .galeria-imagenes {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 1024px) {
  .galeria-imagenes {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    gap: 2rem;
  }
}
.galeria-imagenes .thumb {
  max-width: 25rem;
  margin: 0 auto;
}

.overlay {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7);
  display: grid;
  place-items: center;
}
.overlay picture {
  display: block;
  max-width: 80rem;
  box-shadow: 0 2px 9px #000000;
}
.overlay .btn-cerrar {
  width: 3rem;
  height: 3rem;
  background-color: #ffffff;
  border-radius: 50%;
  font-weight: 700;
  font-size: 2.8rem;
  display: grid;
  place-content: center;
}
.overlay .btn-cerrar:hover {
  cursor: pointer;
  background-color: #bfbfbf;
}

.boletos h3 {
  padding-top: 3rem;
  margin: 0;
  color: #ffffff;
  text-align: center;
}

.contenedor-boletos {
  padding: 3rem 0;
}
@media (min-width: 768px) {
  .contenedor-boletos {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 3rem;
  }
}

.pase .nombre-pase {
  color: #ffffff;
  text-align: center;
  text-transform: uppercase;
  font-size: 1.8rem;
  margin: 3rem 0;
}
@media (min-width: 768px) {
  .pase .nombre-pase {
    margin: 0 0 3rem 0;
  }
}
.pase-header {
  background-color: #F53756;
}
.pase-body {
  list-style: none;
  margin: 0;
  padding: 0;
}
.pase-body li {
  background-color: #ffffff;
  padding: 1rem;
  border-bottom: 0.1rem solid #d9d9d9;
}
.pase-body li:nth-child(odd) {
  background-color: #ffffff;
}
.pase-footer {
  background-color: #752F97;
}
.pase-footer p {
  font-size: 2.4rem;
  font-weight: 700;
}
.pase-header p, .pase-footer p {
  color: #ffffff;
  text-align: center;
  text-transform: uppercase;
  padding: 1rem 0;
}

.footer {
  background-color: #F53756;
  padding: 2rem 0;
}
.footer p {
  color: #ffffff;
  text-align: center;
}